import React, { useEffect, useState } from 'react'
import { Button, Form, Modal, Table } from 'react-bootstrap'
import { fetchAllCourse, updateCourse } from '../../../api/course';
import styled from 'styled-components';
import { STATUS } from '../../../constants/errorCode';

const StyledTable = styled(Table)`
  border-color: rgba(255, 255, 255, 0);
  & * {
    vertical-align: middle;
    text-align: center;
  }
  & tr:first-child {
    width: 20%;
  }
  & tr:nth-child(3), tr:nth-child(4) {
    width: fit-content;
  }
`;

const ManageCourseModal = ({show, handleClose}) => {
  const [courses, setCourses] = useState([]);
  const [editMode, setEditMode] = useState({}); //       
  const [originalNames, setOriginalNames] = useState({}); //     
  const [isFetching, setFetching] = useState(false);

  const getFetchData = async () => {
    try {
      const res = await fetchAllCourse();
      console.log(res.result);
      setCourses(res.result);
    } catch (e) {
      console.error(e);
    }
  }

  useEffect(() => {
    if (!show) return;
    getFetchData();
  }, [show]);

  //   
  const toggleEditMode = (id) => {
    setEditMode(prevState => ({
      ...prevState,
      [id]: !prevState[id]  //      
    }));

    //       
    if (!editMode[id]) {
      setOriginalNames(prevState => ({
        ...prevState,
        [id]: courses.find(course => course.id === id)?.name //   
      }));
    } else {
      //       
      setCourses(prevCourses =>
        prevCourses.map(course =>
          course.id === id ? { ...course, name: originalNames[id] } : course
        )
      );
      //   
      setOriginalNames(prevState => {
        const { [id]: removed, ...rest } = prevState; //    
        return rest;
      });
    }
  }

  //   
  const handleCourseChange = (id, newName) => {
    setCourses(prevCourses =>
      prevCourses.map(course =>
        course.id === id ? { ...course, name: newName } : course
      )
    );
  }

  const updateCourses = async (id) => {
    if (isFetching) {
      alert(' ');
      return;
    }

    try {
      const reqData = { id, name: courses.find(course => course.id === id)?.name }; //  
      console.log(reqData);
      
      const res = await updateCourse(reqData); //  API 
      if(res.status !== STATUS.OK) {
        throw res;
      }
      alert(' .')
      toggleEditMode(id); //      
      getFetchData(); //     
    } catch (e) {
      console.error(e);
    } finally {
      setFetching(false);
    }
  }

  const deleteCourses = (id) => {
    //  API    

    getFetchData(); //     
  }

  return (
    <Modal
        size='lg'
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <StyledTable 
            striped bordered
            responsive={true}
          > 
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th colSpan={2}></th>
              </tr>
            </thead>
            <tbody>
              {
                courses?.length > 0 ? 
                courses.map((c, index) => 
                  <tr key={c?.id}>
                    <td>{index + 1}</td>
                    <td>
                      <Form>
                        <Form.Control 
                          type="text"
                          value={c.name}
                          onChange={(e) => handleCourseChange(c.id, e.target.value)}  //    
                          readOnly={!editMode[c.id]}  //     
                        />
                      </Form>
                    </td>
                    <td>
                      {
                        editMode[c.id] ? 
                        <>
                          <Button variant="success" onClick={() => updateCourses(c.id)}></Button>{' '}
                          <Button variant="secondary" onClick={() => toggleEditMode(c.id)}></Button>
                        </>
                        :
                        <Button variant="primary" onClick={() => toggleEditMode(c.id)}></Button>
                      }
                    </td>
                    <td>
                      <Button variant="danger" onClick={() => deleteCourses(c.id)}></Button>
                    </td>
                  </tr>    
                ) : 
                <tr>
                  <td colSpan={4}>
                     .
                  </td>
                </tr>
              }
            </tbody>
          </StyledTable>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            
          </Button>
        </Modal.Footer>
      </Modal>
  )
}

export default ManageCourseModal;
