import axios from "axios";
import { BEARER, ACCESS_TOKEN } from "../constants/auth";

//  Axios  
const basicRequest = axios.create({
    baseURL: "http://localhost:8080/api", //   URL 
    headers: {
        "Content-Type": 'application/json'
    }
});

//     
basicRequest.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem(ACCESS_TOKEN); // TokenContext token 
        if (token) {
            config.headers.Authorization = `${BEARER}${token}`; //    Authorization  
        }
        return config;
    },
    (error) => 
        Promise.reject(error)
);

// GET  
export const get = async (url) => {
    try {
        const res = await basicRequest.get(url);
        console.log(`GET  :`, res.data); //   
        return res; //   
    } catch (error) {
        //  :   ,   
        console.error(`GET  :`, error.response ? error.response.data : error.message);
        return error.response;
    }
}

// POST  
export const post = async (url, data) => {
    try {
        const res = await basicRequest.post(url, data);
        console.log(`POST  :`, res.data); //   
        return res; 
    } catch (error) {
        //  :   ,   
        console.error(`POST  :`, error.response ? error.response.data : error.message);
        return error.response;
    }
}

//PATCH  
export const patch = async (url, data) => {
    try {
        const res = await basicRequest.patch(url, data);
        console.log(`PATCH  (res.data):`, res.data); //   
        return res; 
    } catch (error) {
        //  :   ,   
        console.error(`POST  :`, error.response ? error.response.data : error.message);
        return error.response;
    }
}
